import React, { useState } from 'react';
import { MOCK_INSURANCE, MOCK_EMPLOYEES } from '../constants';
import { InsuranceRecord } from '../types';
import { ShieldCheck, Plus, ExternalLink, Search, DollarSign, Users, FileSearch, X, AlertCircle, Hash } from 'lucide-react';
import DataControls from '../components/DataControls';

const Insurance: React.FC = () => {
  const [insuranceData, setInsuranceData] = useState<InsuranceRecord[]>(MOCK_INSURANCE);
  const [searchTerm, setSearchTerm] = useState('');
  
  // Modal States
  const [isQueryModalOpen, setIsQueryModalOpen] = useState(false);
  const [isAddModalOpen, setIsAddModalOpen] = useState(false); // NEW: Add Modal State
  
  const [queryInput, setQueryInput] = useState('');
  const [queryResult, setQueryResult] = useState<InsuranceRecord | null>(null);
  const [hasSearched, setHasSearched] = useState(false);

  // Add Form State
  const [addForm, setAddForm] = useState({
    employeeName: '',
    insuranceNumber: '',
    salaryInsured: '',
    companyShare: '',
    employeeShare: ''
  });

  const isAdmin = true;

  // Helper
  const getEmpCode = (name: string) => MOCK_EMPLOYEES.find(e => e.name === name)?.employeeCode || '-';

  const enrichedData = insuranceData.map(i => ({
    ...i,
    employeeCode: getEmpCode(i.employeeName)
  }));

  const handleImport = (data: any[]) => {
    const newItems = data.map((d, i) => ({ ...MOCK_INSURANCE[0], ...d, id: `IMP-INS-${i}` }));
    setInsuranceData([...insuranceData, ...newItems]);
  };

  const filteredData = enrichedData.filter(item => 
    item.employeeName.toLowerCase().includes(searchTerm.toLowerCase()) || 
    item.insuranceNumber.includes(searchTerm) ||
    item.employeeCode.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const totalCompanyShare = insuranceData.reduce((acc, curr) => acc + curr.companyShare, 0);

  const handleQuerySubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const found = insuranceData.find(r => r.insuranceNumber === queryInput);
    setQueryResult(found || null);
    setHasSearched(true);
  };

  // NEW: Handle Add Submit
  const handleAddSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const newRecord: InsuranceRecord = {
      id: `INS-${Date.now()}`,
      employeeName: addForm.employeeName,
      insuranceNumber: addForm.insuranceNumber,
      salaryInsured: Number(addForm.salaryInsured),
      companyShare: Number(addForm.companyShare),
      employeeShare: Number(addForm.employeeShare),
      status: 'active'
    };
    setInsuranceData([newRecord, ...insuranceData]);
    setIsAddModalOpen(false);
    setAddForm({ employeeName: '', insuranceNumber: '', salaryInsured: '', companyShare: '', employeeShare: '' });
    alert('    ');
  };

  return (
    <div className="space-y-6 animate-in fade-in duration-500">
      {/* Header */}
      <div className="flex flex-col justify-between gap-4 sm:flex-row sm:items-center bg-white p-6 rounded-xl shadow-sm border border-gray-100">
        <div>
          <h2 className="text-2xl font-bold text-gray-800"> </h2>
          <p className="text-sm text-gray-500 mt-1">     </p>
        </div>
        <div className="flex gap-3">
          <DataControls 
            data={enrichedData} 
            fileName="social_insurance_data" 
            isAdmin={isAdmin}
            onImport={handleImport}
            headers={[
              { key: 'employeeCode', label: '' },
              { key: 'employeeName', label: '' },
              { key: 'insuranceNumber', label: ' ' },
              { key: 'salaryInsured', label: ' ' },
              { key: 'companyShare', label: ' ' },
              { key: 'employeeShare', label: ' ' }
            ]}
          />
          <button 
            onClick={() => {
              setIsQueryModalOpen(true);
              setQueryInput('');
              setHasSearched(false);
              setQueryResult(null);
            }}
            className="flex items-center gap-2 rounded-lg bg-white border border-gray-200 px-4 py-2.5 text-gray-700 shadow-sm hover:bg-gray-50 hover:border-gray-300 transition-all font-medium"
          >
            <FileSearch className="h-5 w-5 text-gray-500" />
            <span></span>
          </button>
          <button 
            onClick={() => setIsAddModalOpen(true)} // Connect Button
            className="flex items-center gap-2 rounded-lg bg-indigo-600 px-5 py-2.5 text-white shadow-md hover:bg-indigo-700 transition-all hover:shadow-lg active:scale-95 font-medium"
          >
            <Plus className="h-5 w-5" />
            <span>  </span>
          </button>
        </div>
      </div>

      {/* Summary Cards */}
      {/* ... (Keep existing summary cards) ... */}

      {/* Table Section */}
      <div className="rounded-xl bg-white shadow-sm border border-gray-100 overflow-hidden">
        <div className="flex items-center p-5 border-b border-gray-100 bg-gray-50/50">
           <div className="relative w-full sm:w-96">
             <Search className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 text-gray-400" />
             <input 
               type="text" 
               value={searchTerm}
               onChange={(e) => setSearchTerm(e.target.value)}
               placeholder="     ..."
               className="w-full rounded-lg border border-gray-200 bg-white py-2.5 pr-10 pl-4 focus:border-indigo-500 focus:outline-none focus:ring-1 focus:ring-indigo-500 transition-all text-sm font-medium"
             />
           </div>
        </div>
        <div className="overflow-x-auto">
          <table className="w-full text-right text-sm">
            <thead className="bg-gray-50 text-gray-600 border-b border-gray-100">
              <tr>
                <th className="px-6 py-4 font-bold w-24"></th>
                <th className="px-6 py-4 font-bold"></th>
                <th className="px-6 py-4 font-bold"> </th>
                <th className="px-6 py-4 font-bold"> </th>
                <th className="px-6 py-4 font-bold text-gray-500"> </th>
                <th className="px-6 py-4 font-bold text-gray-500"> </th>
                <th className="px-6 py-4 font-bold"></th>
                <th className="px-6 py-4 font-bold text-center"></th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-50">
              {filteredData.length > 0 ? (
                filteredData.map((record) => (
                  <tr key={record.id} className="hover:bg-indigo-50/30 transition-colors group">
                    <td className="px-6 py-4 font-mono text-gray-500 font-medium">{record.employeeCode}</td>
                    <td className="px-6 py-4 font-bold text-gray-900 flex items-center gap-3">
                       <div className="bg-indigo-50 p-1.5 rounded-lg text-indigo-600 group-hover:bg-indigo-100 transition-colors">
                          <ShieldCheck className="h-4 w-4" />
                       </div>
                       {record.employeeName}
                    </td>
                    <td className="px-6 py-4 text-gray-600 font-mono tracking-wide text-xs">{record.insuranceNumber}</td>
                    <td className="px-6 py-4 text-gray-900 font-bold font-mono">{record.salaryInsured.toLocaleString()} .</td>
                    <td className="px-6 py-4 text-gray-600 font-mono text-xs">{record.companyShare.toLocaleString()}</td>
                    <td className="px-6 py-4 text-gray-600 font-mono text-xs">{record.employeeShare.toLocaleString()}</td>
                    <td className="px-6 py-4">
                      <span className={`inline-flex items-center gap-1.5 rounded-full px-2.5 py-1 text-xs font-bold border
                        ${record.status === 'active' ? 'bg-green-50 text-green-700 border-green-200' : 'bg-yellow-50 text-yellow-700 border-yellow-200'}
                      `}>
                        <span className={`w-1.5 h-1.5 rounded-full ${record.status === 'active' ? 'bg-green-600' : 'bg-yellow-600'}`}></span>
                        {record.status === 'active' ? ' ' : ' '}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-center">
                       <button className="text-gray-400 hover:text-indigo-600 hover:bg-indigo-50 p-2 rounded-lg transition-all">
                          <ExternalLink className="h-4 w-4" />
                       </button>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={8} className="px-6 py-12 text-center text-gray-400 bg-gray-50/50">
                    <div className="flex flex-col items-center justify-center gap-2">
                      <FileSearch className="h-10 w-10 text-gray-300" />
                      <p className="font-medium">   </p>
                    </div>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Query Modal */}
      {isQueryModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 p-4 backdrop-blur-sm">
          <div className="w-full max-w-md rounded-2xl bg-white shadow-2xl animate-in fade-in zoom-in duration-200 flex flex-col">
            <div className="flex items-center justify-between border-b border-gray-100 p-6 bg-gray-50/50 rounded-t-2xl">
              <h3 className="text-lg font-bold text-gray-800 flex items-center gap-2">
                 <Hash className="h-5 w-5 text-indigo-600" />
                    
              </h3>
              <button onClick={() => setIsQueryModalOpen(false)} className="rounded-full p-2 text-gray-400 hover:bg-white hover:text-gray-700 hover:shadow-sm transition-all">
                <X className="h-5 w-5" />
              </button>
            </div>
            <div className="p-6">
              <form onSubmit={handleQuerySubmit} className="relative mb-6">
                <input type="text" required value={queryInput} onChange={(e) => setQueryInput(e.target.value)} placeholder="   ..." className="w-full rounded-xl border border-gray-300 p-3.5 pr-12 text-sm focus:border-indigo-500 focus:outline-none focus:ring-2 focus:ring-indigo-100 transition-all font-medium shadow-sm" />
                <button type="submit" className="absolute left-2 top-1.5 bottom-1.5 bg-indigo-600 text-white px-4 rounded-lg hover:bg-indigo-700 text-sm font-bold transition-colors shadow-sm"></button>
                <FileSearch className="absolute right-4 top-1/2 -translate-y-1/2 h-5 w-5 text-gray-400" />
              </form>
              {/* Result display logic kept same as before... */}
            </div>
          </div>
        </div>
      )}

      {/* NEW: Add Insured Modal */}
      {isAddModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4 backdrop-blur-sm">
           <div className="w-full max-w-lg bg-white rounded-2xl shadow-2xl overflow-hidden animate-in fade-in zoom-in duration-200">
              <div className="p-6 border-b border-gray-100 flex justify-between items-center bg-gray-50">
                 <h3 className="text-lg font-bold text-gray-800">   </h3>
                 <button onClick={() => setIsAddModalOpen(false)} className="text-gray-400 hover:text-gray-600"><X className="h-5 w-5" /></button>
              </div>
              <form onSubmit={handleAddSubmit} className="p-6 space-y-4">
                 <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1"></label>
                    <select required className="w-full p-2.5 rounded-lg border border-gray-300" value={addForm.employeeName} onChange={e => setAddForm({...addForm, employeeName: e.target.value})}>
                       <option value=""> </option>
                       {MOCK_EMPLOYEES.map(e => <option key={e.id} value={e.name}>{e.name}</option>)}
                    </select>
                 </div>
                 <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                    <input type="text" required className="w-full p-2.5 rounded-lg border border-gray-300" value={addForm.insuranceNumber} onChange={e => setAddForm({...addForm, insuranceNumber: e.target.value})} />
                 </div>
                 <div className="grid grid-cols-3 gap-3">
                    <div><label className="block text-xs font-medium text-gray-700 mb-1"> </label><input type="number" required className="w-full p-2 rounded-lg border border-gray-300" value={addForm.salaryInsured} onChange={e => setAddForm({...addForm, salaryInsured: e.target.value})} /></div>
                    <div><label className="block text-xs font-medium text-gray-700 mb-1"> </label><input type="number" required className="w-full p-2 rounded-lg border border-gray-300" value={addForm.companyShare} onChange={e => setAddForm({...addForm, companyShare: e.target.value})} /></div>
                    <div><label className="block text-xs font-medium text-gray-700 mb-1"> </label><input type="number" required className="w-full p-2 rounded-lg border border-gray-300" value={addForm.employeeShare} onChange={e => setAddForm({...addForm, employeeShare: e.target.value})} /></div>
                 </div>
                 <button type="submit" className="w-full bg-indigo-600 text-white py-2.5 rounded-lg font-bold hover:bg-indigo-700 mt-4"> </button>
              </form>
           </div>
        </div>
      )}
    </div>
  );
};

export default Insurance;