import React, { useState } from 'react';
import { MOCK_CONTRACTS, MOCK_CONTRACT_HISTORY, MOCK_EMPLOYEES } from '../constants';
import { Contract } from '../types';
import { FileText, Plus, AlertTriangle, CheckCircle, Clock, X, History, FileEdit, Search, Filter, ChevronDown, Calendar } from 'lucide-react';
import DataControls from '../components/DataControls';

const Contracts: React.FC = () => {
  const [contracts, setContracts] = useState<Contract[]>(MOCK_CONTRACTS);
  const [selectedContract, setSelectedContract] = useState<Contract | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterStatus, setFilterStatus] = useState('all');
  
  // NEW: Modal State
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [addForm, setAddForm] = useState({
    employeeName: '',
    type: ' ' as any,
    startDate: '',
    endDate: ''
  });

  const isAdmin = true;

  // Helper to get code
  const getEmpCode = (name: string) => MOCK_EMPLOYEES.find(e => e.name === name)?.employeeCode || '-';

  // Enrich data
  const enrichedContracts = contracts.map(c => ({
    ...c,
    employeeCode: getEmpCode(c.employeeName)
  }));

  const handleImport = (data: any[]) => {
    const newItems = data.map((d, i) => ({ ...MOCK_CONTRACTS[0], ...d, id: `IMP-CON-${i}` }));
    setContracts([...contracts, ...newItems]);
  };

  const getContractHistory = (contractId: string) => {
    return MOCK_CONTRACT_HISTORY.filter(h => h.contractId === contractId).sort((a, b) => 
      new Date(b.date).getTime() - new Date(a.date).getTime()
    );
  };

  const filteredContracts = enrichedContracts.filter(c => {
    const matchesSearch = c.employeeName.toLowerCase().includes(searchTerm.toLowerCase()) ||
                          c.employeeCode.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesStatus = filterStatus === 'all' || c.status === filterStatus;
    return matchesSearch && matchesStatus;
  });

  // NEW: Handle Add Submit
  const handleAddSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const newContract: Contract = {
       id: `CON-${Date.now()}`,
       employeeName: addForm.employeeName,
       type: addForm.type,
       startDate: addForm.startDate,
       endDate: addForm.endDate,
       status: 'active'
    };
    setContracts([newContract, ...contracts]);
    setIsAddModalOpen(false);
    setAddForm({ employeeName: '', type: ' ', startDate: '', endDate: '' });
    window.alert('   ');
  };

  return (
    <div className="space-y-6 animate-in fade-in duration-500">
      {/* Header */}
      <div className="flex flex-col justify-between gap-4 sm:flex-row sm:items-center bg-white p-6 rounded-xl shadow-sm border border-gray-100">
        <div>
          <h2 className="text-2xl font-bold text-gray-800"> </h2>
          <p className="text-sm text-gray-500 mt-1">     </p>
        </div>
        <div className="flex gap-3">
          <DataControls 
            data={enrichedContracts} 
            fileName="contracts_data" 
            isAdmin={isAdmin}
            onImport={handleImport}
            headers={[
              { key: 'employeeCode', label: '' },
              { key: 'employeeName', label: '' },
              { key: 'type', label: ' ' },
              { key: 'startDate', label: ' ' },
              { key: 'endDate', label: ' ' },
              { key: 'status', label: '' }
            ]}
          />
          <button onClick={() => setIsAddModalOpen(true)} className="flex items-center gap-2 rounded-lg bg-indigo-600 px-5 py-2.5 text-white shadow-md hover:bg-indigo-700 transition-all hover:shadow-lg active:scale-95 font-medium">
            <Plus className="h-5 w-5" />
            <span> </span>
          </button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100 flex items-center justify-between hover:shadow-md transition-all group">
          <div>
            <p className="text-gray-500 text-sm font-medium group-hover:text-green-600 transition-colors"> </p>
            <p className="text-3xl font-bold text-gray-800 mt-2">{contracts.filter(c => c.status === 'active').length}</p>
          </div>
          <div className="bg-green-50 p-4 rounded-full group-hover:bg-green-100 transition-colors">
            <CheckCircle className="h-8 w-8 text-green-600" />
          </div>
        </div>
        {/* ... (Other Stats Cards) ... */}
      </div>

      {/* Filters & Table */}
      <div className="rounded-xl bg-white shadow-sm border border-gray-100 overflow-hidden">
        <div className="flex flex-col gap-4 border-b border-gray-100 p-5 sm:flex-row sm:items-center bg-gray-50/30">
          <div className="relative flex-1">
            <Search className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 text-gray-400" />
            <input 
              type="text" 
              placeholder="    ..."
              className="w-full rounded-lg border border-gray-200 bg-white py-2.5 pr-10 pl-4 focus:border-indigo-500 focus:outline-none focus:ring-1 focus:ring-indigo-500 transition-all text-sm"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
          <div className="flex items-center gap-2 relative">
            <Filter className="absolute right-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-500 pointer-events-none" />
            <select
              value={filterStatus}
              onChange={(e) => setFilterStatus(e.target.value)}
              className="appearance-none rounded-lg border border-gray-200 bg-white py-2.5 pr-9 pl-8 text-sm text-gray-700 focus:border-indigo-500 focus:outline-none cursor-pointer hover:border-gray-300 transition-colors w-48"
            >
              <option value="all"> </option>
              <option value="active"></option>
              <option value="expiring"> </option>
              <option value="expired"></option>
            </select>
            <ChevronDown className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400 pointer-events-none" />
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="w-full text-right text-sm">
            <thead className="bg-gray-50 text-gray-600 border-b border-gray-100">
              <tr>
                <th className="px-6 py-4 font-bold w-24"></th>
                <th className="px-6 py-4 font-bold"></th>
                <th className="px-6 py-4 font-bold"> </th>
                <th className="px-6 py-4 font-bold"> </th>
                <th className="px-6 py-4 font-bold"> </th>
                <th className="px-6 py-4 font-bold"></th>
                <th className="px-6 py-4 font-bold text-center"></th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-50">
              {filteredContracts.length > 0 ? (
                filteredContracts.map((contract) => (
                  <tr key={contract.id} className="hover:bg-indigo-50/30 transition-colors group">
                    <td className="px-6 py-4 font-mono text-gray-500 font-medium">{contract.employeeCode}</td>
                    <td className="px-6 py-4 font-bold text-gray-800">{contract.employeeName}</td>
                    <td className="px-6 py-4 text-gray-600">
                        <span className="bg-gray-100 text-gray-700 px-2 py-1 rounded text-xs border border-gray-200">{contract.type}</span>
                    </td>
                    <td className="px-6 py-4 text-gray-600 font-mono text-xs">
                        <div className="flex items-center gap-1">
                           <Calendar className="h-3 w-3 text-gray-400" />
                           {contract.startDate}
                        </div>
                    </td>
                    <td className="px-6 py-4 text-gray-600 font-mono text-xs">
                        <div className="flex items-center gap-1">
                           <Calendar className="h-3 w-3 text-gray-400" />
                           {contract.endDate}
                        </div>
                    </td>
                    <td className="px-6 py-4">
                      <span className={`inline-flex items-center gap-1.5 rounded-full px-2.5 py-1 text-xs font-bold border
                        ${contract.status === 'active' ? 'bg-green-50 text-green-700 border-green-200' : 
                          contract.status === 'expiring' ? 'bg-yellow-50 text-yellow-700 border-yellow-200' : 'bg-red-50 text-red-700 border-red-200'}
                      `}>
                        <span className={`w-1.5 h-1.5 rounded-full ${
                          contract.status === 'active' ? 'bg-green-600' : 
                          contract.status === 'expiring' ? 'bg-yellow-600' : 'bg-red-600'
                        }`}></span>
                        {contract.status === 'active' ? '' : contract.status === 'expiring' ? ' ' : ''}
                      </span>
                    </td>
                    <td className="px-6 py-4">
                       <div className="flex items-center justify-center gap-2">
                         <button 
                            onClick={() => setSelectedContract(contract)}
                            className="text-indigo-600 bg-indigo-50 hover:bg-indigo-100 p-2 rounded-lg transition-colors flex items-center gap-1 border border-indigo-100"
                            title=" "
                          >
                            <History className="h-4 w-4" />
                         </button>
                         <button className="text-gray-500 bg-gray-50 hover:bg-gray-100 hover:text-gray-700 p-2 rounded-lg transition-colors border border-gray-200" title=" ">
                            <FileText className="h-4 w-4" />
                         </button>
                       </div>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={7} className="px-6 py-12 text-center text-gray-400 bg-gray-50/50">
                    <div className="flex flex-col items-center justify-center gap-2">
                      <FileText className="h-10 w-10 text-gray-300" />
                      <p className="font-medium">    </p>
                    </div>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* NEW: Add Contract Modal */}
      {isAddModalOpen && (
         <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4 backdrop-blur-sm">
            <div className="w-full max-w-md bg-white rounded-2xl shadow-2xl overflow-hidden animate-in fade-in zoom-in duration-200">
               <div className="p-6 border-b border-gray-100 flex justify-between items-center bg-gray-50">
                  <h3 className="text-lg font-bold text-gray-800">  </h3>
                  <button onClick={() => setIsAddModalOpen(false)} className="text-gray-400 hover:text-gray-600"><X className="h-5 w-5" /></button>
               </div>
               <form onSubmit={handleAddSubmit} className="p-6 space-y-4">
                  <div>
                     <label className="block text-sm font-medium text-gray-700 mb-1"></label>
                     <select required className="w-full p-2.5 rounded-lg border border-gray-300" value={addForm.employeeName} onChange={e => setAddForm({...addForm, employeeName: e.target.value})}>
                        <option value=""> </option>
                        {MOCK_EMPLOYEES.map(e => <option key={e.id} value={e.name}>{e.name}</option>)}
                     </select>
                  </div>
                  <div>
                     <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                     <select className="w-full p-2.5 rounded-lg border border-gray-300" value={addForm.type} onChange={e => setAddForm({...addForm, type: e.target.value as any})}>
                        <option value=" "> </option>
                        <option value="  ">  </option>
                        <option value=" "> </option>
                     </select>
                  </div>
                  <div className="grid grid-cols-2 gap-4">
                     <div><label className="block text-sm font-medium text-gray-700 mb-1"> </label><input type="date" required className="w-full p-2.5 rounded-lg border border-gray-300" value={addForm.startDate} onChange={e => setAddForm({...addForm, startDate: e.target.value})} /></div>
                     <div><label className="block text-sm font-medium text-gray-700 mb-1"> </label><input type="date" required className="w-full p-2.5 rounded-lg border border-gray-300" value={addForm.endDate} onChange={e => setAddForm({...addForm, endDate: e.target.value})} /></div>
                  </div>
                  <button type="submit" className="w-full bg-indigo-600 text-white py-2.5 rounded-lg font-bold hover:bg-indigo-700 mt-4"> </button>
               </form>
            </div>
         </div>
      )}

      {/* Details Modal */}
      {selectedContract && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 p-4 backdrop-blur-sm">
           <div className="w-full max-w-2xl rounded-2xl bg-white shadow-2xl animate-in fade-in zoom-in duration-200 flex flex-col max-h-[90vh] border border-gray-100">
              <div className="flex items-center justify-between border-b border-gray-100 p-6 bg-gray-50/50 rounded-t-2xl">
                <div>
                   <h3 className="text-xl font-bold text-gray-800 flex items-center gap-2">
                      <div className="bg-indigo-100 p-2 rounded-lg">
                         <FileEdit className="h-5 w-5 text-indigo-600" />
                      </div>
                        
                   </h3>
                   <p className="text-sm text-gray-500 mt-1 mr-11">    <span className="font-bold text-gray-900">{selectedContract.employeeName}</span></p>
                </div>
                <button 
                  onClick={() => setSelectedContract(null)}
                  className="rounded-full p-2 text-gray-400 hover:bg-white hover:text-gray-700 hover:shadow-sm transition-all"
                >
                  <X className="h-6 w-6" />
                </button>
              </div>

              <div className="p-6 overflow-y-auto flex-1 custom-scrollbar">
                 {/* Current Contract Info */}
                 <div className="bg-white rounded-xl p-5 mb-8 border border-indigo-100 shadow-sm ring-1 ring-indigo-50">
                    <h4 className="text-indigo-800 font-bold text-sm mb-4 flex items-center gap-2">
                      <CheckCircle className="h-4 w-4" />
                       
                    </h4>
                    <div className="grid grid-cols-2 gap-y-6 gap-x-8">
                       <div>
                          <p className="text-xs text-gray-500 mb-1 font-medium"> </p>
                          <p className="font-bold text-gray-900 text-sm">{selectedContract.type}</p>
                       </div>
                       <div>
                          <p className="text-xs text-gray-500 mb-1 font-medium"></p>
                          <span className={`inline-flex items-center rounded-full px-2.5 py-0.5 text-xs font-bold
                             ${selectedContract.status === 'active' ? 'bg-green-100 text-green-700' : 'bg-red-100 text-red-700'}`}>
                             {selectedContract.status === 'active' ? '' : ''}
                          </span>
                       </div>
                       <div>
                          <p className="text-xs text-gray-500 mb-1 font-medium"> </p>
                          <p className="font-mono font-bold text-gray-900 text-sm">{selectedContract.startDate}</p>
                       </div>
                       <div>
                          <p className="text-xs text-gray-500 mb-1 font-medium"> </p>
                          <p className="font-mono font-bold text-gray-900 text-sm">{selectedContract.endDate}</p>
                       </div>
                    </div>
                 </div>

                 <h4 className="font-bold text-gray-800 mb-6 flex items-center gap-2 border-b border-gray-100 pb-3">
                    <History className="h-5 w-5 text-gray-400" />
                      
                 </h4>

                 {/* Timeline */}
                 <div className="relative border-r-2 border-gray-100 mr-3 pr-8 space-y-8 pb-4">
                    {getContractHistory(selectedContract.id).length > 0 ? (
                       getContractHistory(selectedContract.id).map((event) => (
                          <div key={event.id} className="relative group">
                             {/* Dot */}
                             <div className="absolute -right-[39px] top-1.5 h-5 w-5 rounded-full border-4 border-white bg-indigo-100 shadow-sm group-hover:bg-indigo-600 transition-colors"></div>
                             
                             <div className="flex flex-col sm:flex-row sm:items-start justify-between gap-2 bg-gray-50 p-4 rounded-xl border border-gray-100 group-hover:border-indigo-100 group-hover:shadow-sm transition-all">
                                <div>
                                   <p className="font-bold text-gray-800 text-sm">{event.type}</p>
                                   <p className="text-gray-600 text-sm mt-1 leading-relaxed">{event.details}</p>
                                   {event.documentUrl && (
                                      <button className="mt-3 text-indigo-700 text-xs flex items-center gap-1 hover:underline font-bold bg-indigo-50 border border-indigo-100 px-3 py-1.5 rounded-lg w-fit transition-colors hover:bg-indigo-100">
                                         <FileText className="h-3 w-3" />
                                          
                                      </button>
                                   )}
                                </div>
                                <div className="text-left sm:text-left shrink-0">
                                   <span className="text-xs font-mono font-bold text-indigo-600 bg-white border border-indigo-50 px-2 py-1 rounded block mb-1 shadow-sm">
                                      {event.date}
                                   </span>
                                   <p className="text-[10px] text-gray-400">: {event.changedBy}</p>
                                </div>
                             </div>
                          </div>
                       ))
                    ) : (
                       <p className="text-gray-400 text-sm italic bg-gray-50 p-4 rounded-lg text-center">      .</p>
                    )}
                 </div>
              </div>

              <div className="p-6 border-t border-gray-100 bg-gray-50 rounded-b-2xl flex justify-end">
                 <button 
                   onClick={() => setSelectedContract(null)}
                   className="px-6 py-2.5 bg-white border border-gray-300 rounded-xl text-gray-700 hover:bg-gray-50 text-sm font-bold shadow-sm transition-colors"
                 >
                   
                 </button>
              </div>
           </div>
        </div>
      )}
    </div>
  );
};

export default Contracts;