
import React, { useState } from 'react';
import { MOCK_ATTENDANCE, MOCK_EMPLOYEES, MOCK_DEVICES } from '../constants';
import { BiometricDevice, AttendanceRecord } from '../types';
import { Search, Calendar as CalendarIcon, Clock, CheckCircle, XCircle, AlertTriangle, MapPin, Cpu, RefreshCw, Plus, X, Wifi, WifiOff, Fingerprint, Trash2, Activity, Radar, UserPlus, ServerCrash } from 'lucide-react';
import DataControls from '../components/DataControls';
import { api } from '../services/api';

const Attendance: React.FC = () => {
  const [date, setDate] = useState('2023-10-25');
  const [attendanceData, setAttendanceData] = useState<AttendanceRecord[]>(MOCK_ATTENDANCE);
  const [devices, setDevices] = useState<BiometricDevice[]>(MOCK_DEVICES);
  const [searchTerm, setSearchTerm] = useState('');
  
  // Modal States
  const [isDeviceModalOpen, setIsDeviceModalOpen] = useState(false);
  const [isAddDeviceMode, setIsAddDeviceMode] = useState(false);
  const [isSyncing, setIsSyncing] = useState(false);
  
  // Scan States
  const [isScanning, setIsScanning] = useState(false);
  const [discoveredDevices, setDiscoveredDevices] = useState<any[]>([]);

  // New Device Form
  const [newDevice, setNewDevice] = useState({
    name: '',
    ip: '',
    port: '4370',
    type: 'ZK',
    location: ''
  });

  const isAdmin = true;

  // Helper to get employee code
  const getEmpCode = (name: string) => MOCK_EMPLOYEES.find(e => e.name === name)?.employeeCode || '-';

  // Enrich data with employee code for export/display
  const filteredData = attendanceData.filter(d => {
     // If we have a stored code, search by it, otherwise try to resolve it
     const code = d.employeeCode || getEmpCode(d.employeeName);
     return d.employeeName.toLowerCase().includes(searchTerm.toLowerCase()) ||
            code.toLowerCase().includes(searchTerm.toLowerCase());
  });

  const handleImport = (data: any[]) => {
    setAttendanceData([...attendanceData, ...data.map((d, i) => ({
      ...d, 
      id: `IMP-ATT-${i}`
    }))]);
  };

  // --- Device Functions ---
  const handleAddDevice = (e: React.FormEvent) => {
    e.preventDefault();
    addDeviceToList(newDevice);
  };

  const addDeviceToList = (deviceData: any) => {
    const device: BiometricDevice = {
      id: `DEV-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      name: deviceData.name,
      ip: deviceData.ip,
      port: deviceData.port,
      type: deviceData.type as any,
      location: deviceData.location,
      status: 'online', 
      lastSync: ' '
    };
    setDevices([...devices, device]);
    setIsAddDeviceMode(false);
    setNewDevice({ name: '', ip: '', port: '4370', type: 'ZK', location: '' });
    // Remove from discovered if exists
    setDiscoveredDevices(prev => prev.filter(d => d.ip !== deviceData.ip));
  };

  const handleDeleteDevice = (id: string) => {
    if (window.confirm('               .')) {
      setDevices(devices.filter(d => d.id !== id));
    }
  };

  const handlePingDevice = async (device: BiometricDevice) => {
     try {
       await api.checkHealth();
       alert(`  .\n    ${device.ip}...`);
     } catch (e) {
       alert(`   .\n    server.js`);
     }
  };

  const handleScanNetwork = async () => {
    setIsScanning(true);
    setDiscoveredDevices([]);
    
    try {
      console.log('Scanning network via backend...');
      const response = await api.scanForDevices();
      
      if (response.success && response.devices.length > 0) {
        setDiscoveredDevices(response.devices);
      } else {
        alert('         ZK    4370.');
      }
    } catch (e) {
      console.warn("Backend unavailable");
      alert('  .     (node server.js).');
    } finally {
      setIsScanning(false);
    }
  };

  const handleSync = async () => {
    setIsSyncing(true);
    const targetDevice = devices.find(d => d.status === 'online');
    
    if (!targetDevice) {
        alert('    .    .');
        setIsSyncing(false);
        return;
    }

    try {
        const response = await api.syncAttendance(targetDevice.ip, targetDevice.port, targetDevice.type);
        
        // Check strict success
        if (response && response.success) {
            processSyncData(response.data, targetDevice.id);
            alert(response.message || '   ');
        } else {
            // Show the REAL error message from server
            alert(`    :\n${response?.message || '  '}`);
        }
    } catch (error: any) {
        console.error('Sync Error:', error);
        // This usually means the server.js is not running or unreachable
        alert(`    (Backend).\n: ${error.message}`);
    } finally {
        setIsSyncing(false);
    }
  };

  const processSyncData = (records: any[], deviceId: string) => {
    const now = new Date().toLocaleString('ar-EG');
    setDevices(devices.map(d => d.id === deviceId ? { ...d, lastSync: now } : d));
    
    // Logic to Match Backend Data with Employees
    const newRecords: AttendanceRecord[] = records.map((log: any) => {
        // Find employee by Code
        // Ensure strictly string comparison
        const logCodeStr = String(log.employeeCode).trim();
        const employee = MOCK_EMPLOYEES.find(e => e.employeeCode === logCodeStr);
        
        return {
            id: log.id,
            employeeId: employee ? employee.id : 'UNKNOWN',
            employeeName: employee ? employee.name : `   (${logCodeStr})`,
            employeeCode: logCodeStr,
            date: log.date,
            checkIn: log.status === 'CheckIn' ? log.timestamp : '-',
            checkOut: log.status === 'CheckOut' ? log.timestamp : '-',
            status: employee ? 'present' : 'absent',
            workHours: 0, 
            source: 'Fingerprint'
        };
    });

    // Prepend new records so they show at top
    setAttendanceData(prev => [...newRecords, ...prev]);
  };

  // Helper to check time deviations
  const isLate = (time: string) => {
    if (!time || time === '-') return false;
    const [hours] = time.split(':').map(Number);
    return hours >= 9 && parseInt(time.split(':')[1]) > 15; 
  };

  const isEarly = (time: string) => {
    if (!time || time === '-') return false;
    const [hours] = time.split(':').map(Number);
    return hours < 16; 
  };

  const stats = [
    { title: ' ', value: attendanceData.filter(d => d.status === 'present').length.toString(), color: 'text-green-600', bg: 'bg-green-50', border: 'border-green-200', icon: CheckCircle },
    { title: '', value: attendanceData.filter(d => d.status === 'absent').length.toString(), color: 'text-red-600', bg: 'bg-red-50', border: 'border-red-200', icon: XCircle },
    { title: '', value: attendanceData.filter(d => d.status === 'late').length.toString(), color: 'text-yellow-600', bg: 'bg-yellow-50', border: 'border-yellow-200', icon: Clock },
    { title: '', value: attendanceData.filter(d => d.status === 'excused').length.toString(), color: 'text-indigo-600', bg: 'bg-indigo-50', border: 'border-indigo-200', icon: MapPin },
  ];

  return (
    <div className="space-y-6 animate-in fade-in duration-500 h-[calc(100vh-8rem)] flex flex-col">
      {/* Header */}
      <div className="flex flex-col justify-between gap-4 sm:flex-row sm:items-center bg-white p-6 rounded-xl shadow-sm border border-gray-100 shrink-0">
        <div>
          <h2 className="text-2xl font-bold text-gray-800">  </h2>
          <p className="text-sm text-gray-500 mt-1">     </p>
        </div>
        <div className="flex items-center gap-2">
           <button 
             onClick={() => setIsDeviceModalOpen(true)}
             className="flex items-center gap-2 rounded-lg bg-slate-800 text-white px-4 py-2.5 shadow-md hover:bg-slate-900 transition-all"
           >
             <Cpu className="h-5 w-5" />
             <span> </span>
           </button>
           <DataControls 
              data={filteredData} 
              fileName="attendance_report" 
              isAdmin={isAdmin}
              onImport={handleImport}
              headers={[
                { key: 'employeeCode', label: '' },
                { key: 'employeeName', label: '' },
                { key: 'date', label: '' },
                { key: 'checkIn', label: '' },
                { key: 'checkOut', label: '' },
                { key: 'workHours', label: '' },
                { key: 'status', label: '' }
              ]}
           />
        </div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 shrink-0">
         {stats.map((stat, idx) => (
           <div key={idx} className={`bg-white p-5 rounded-xl shadow-sm border ${stat.border} hover:shadow-md transition-all`}>
              <div className="flex justify-between items-start">
                <div>
                  <p className="text-gray-500 text-sm font-medium mb-1">{stat.title}</p>
                  <p className={`text-3xl font-bold ${stat.color}`}>{stat.value}</p>
                </div>
                <div className={`${stat.bg} p-3 rounded-full`}>
                  <stat.icon className={`h-6 w-6 ${stat.color}`} />
                </div>
              </div>
           </div>
         ))}
      </div>

      {/* Main Content */}
      <div className="rounded-xl bg-white shadow-sm border border-gray-100 overflow-hidden flex flex-col flex-1 min-h-0">
        {/* Toolbar */}
        <div className="flex flex-col gap-4 border-b border-gray-100 p-5 sm:flex-row sm:items-center justify-between bg-gray-50/50 shrink-0">
          <div className="relative w-full sm:w-64">
             <CalendarIcon className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 text-gray-400 pointer-events-none" />
             <input 
                type="date" 
                value={date} 
                onChange={e => setDate(e.target.value)} 
                className="w-full rounded-lg border border-gray-200 bg-white py-2.5 pr-10 pl-3 text-gray-700 focus:border-indigo-500 focus:outline-none focus:ring-1 focus:ring-indigo-500 transition-all cursor-pointer" 
             />
          </div>
          <div className="relative w-full sm:w-80">
            <Search className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 text-gray-400" />
            <input 
              type="text" 
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder="    ..."
              className="w-full rounded-lg border border-gray-200 bg-white py-2.5 pr-10 pl-4 focus:border-indigo-500 focus:outline-none focus:ring-1 focus:ring-indigo-500 transition-all"
            />
          </div>
        </div>

        {/* Table */}
        <div className="overflow-auto flex-1">
          <table className="w-full text-right text-sm border-collapse">
            <thead className="bg-gray-50 text-gray-500 border-b border-gray-100 sticky top-0 z-10 shadow-sm">
              <tr>
                <th className="px-6 py-4 font-semibold bg-gray-50 w-24"></th>
                <th className="px-6 py-4 font-semibold bg-gray-50"></th>
                <th className="px-6 py-4 font-semibold bg-gray-50"></th>
                <th className="px-6 py-4 font-semibold bg-gray-50"></th>
                <th className="px-6 py-4 font-semibold bg-gray-50"></th>
                <th className="px-6 py-4 font-semibold bg-gray-50"></th>
                <th className="px-6 py-4 font-semibold bg-gray-50"></th>
                <th className="px-6 py-4 font-semibold bg-gray-50"></th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-50">
              {filteredData.length > 0 ? (
                filteredData.map((record) => (
                  <tr key={record.id} className="hover:bg-indigo-50/30 transition-colors">
                    <td className="px-6 py-4 font-mono text-gray-500">
                      {record.employeeCode || getEmpCode(record.employeeName)}
                    </td>
                    <td className="px-6 py-4 font-medium text-gray-900">
                        {record.employeeName.includes(' ') ? (
                            <div className="flex items-center gap-2">
                                <span className="text-red-600 bg-red-50 px-2 py-1 rounded-lg border border-red-100 flex items-center gap-1 font-bold">
                                    <AlertTriangle className="h-3 w-3" />
                                     
                                </span>
                                <button className="text-xs text-indigo-600 hover:underline flex items-center gap-1" title=" ">
                                    <UserPlus className="h-3 w-3" />
                                    
                                </button>
                            </div>
                        ) : (
                            record.employeeName
                        )}
                    </td>
                    <td className="px-6 py-4 text-gray-600 font-mono tracking-wide">{record.date}</td>
                    <td className="px-6 py-4 text-gray-600 font-mono relative">
                       {record.checkIn}
                       {isLate(record.checkIn) && (
                          <span className="absolute -top-1 right-2 w-2 h-2 bg-red-500 rounded-full" title=""></span>
                       )}
                    </td>
                    <td className="px-6 py-4 text-gray-600 font-mono relative">
                       {record.checkOut}
                       {isEarly(record.checkOut) && (
                          <span className="absolute -top-1 right-2 w-2 h-2 bg-yellow-500 rounded-full" title=" "></span>
                       )}
                    </td>
                    <td className="px-6 py-4 text-gray-900 font-medium">{record.workHours}</td>
                    <td className="px-6 py-4 text-gray-500 text-xs">
                       {record.source === 'Fingerprint' && <span className="flex items-center gap-1"><Fingerprint className="h-3 w-3" /> </span>}
                       {record.source === 'Manual' && <span className="flex items-center gap-1"> </span>}
                    </td>
                    <td className="px-6 py-4">
                      <span className={`inline-flex items-center gap-1.5 rounded-full px-3 py-1 text-xs font-medium border
                        ${
                          record.status === 'present' ? 'bg-green-50 text-green-700 border-green-100' : 
                          record.status === 'absent' ? 'bg-red-50 text-red-700 border-red-100' :
                          record.status === 'late' ? 'bg-yellow-50 text-yellow-700 border-yellow-100' :
                          'bg-indigo-50 text-indigo-700 border-indigo-100'
                        }
                      `}>
                        {record.status === 'present' && <CheckCircle className="h-3 w-3" />}
                        {record.status === 'absent' && <XCircle className="h-3 w-3" />}
                        {record.status === 'late' && <AlertTriangle className="h-3 w-3" />}
                        {record.status === 'excused' && <MapPin className="h-3 w-3" />}
                        
                        {record.status === 'present' ? '' : record.status === 'absent' ? '' : record.status === 'late' ? '' : ''}
                      </span>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={8} className="px-6 py-12 text-center text-gray-400 bg-gray-50">
                    <div className="flex flex-col items-center justify-center gap-2">
                      <Clock className="h-8 w-8 text-gray-300" />
                      <p>   </p>
                    </div>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Devices Modal */}
      {isDeviceModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4 backdrop-blur-sm">
           <div className="w-full max-w-3xl rounded-2xl bg-white shadow-2xl animate-in fade-in zoom-in duration-200 flex flex-col max-h-[90vh]">
              <div className="flex items-center justify-between border-b border-gray-100 p-6 bg-slate-50 rounded-t-2xl">
                 <div>
                    <h3 className="text-xl font-bold text-slate-800 flex items-center gap-2">
                       <Cpu className="h-6 w-6 text-indigo-600" />
                         
                    </h3>
                    <p className="text-sm text-slate-500 mt-1">      (ZK, Timy)</p>
                 </div>
                 <button onClick={() => setIsDeviceModalOpen(false)} className="rounded-full p-2 text-gray-400 hover:bg-gray-200 hover:text-gray-700 transition">
                    <X className="h-6 w-6" />
                 </button>
              </div>

              <div className="p-6 overflow-y-auto flex-1">
                 <div className="flex flex-wrap justify-between items-center mb-4 gap-3">
                    <div className="flex items-center gap-3">
                       <h4 className="font-bold text-slate-700"> </h4>
                       <span className="bg-slate-100 text-slate-600 px-2 py-0.5 rounded text-xs font-bold">{devices.length}</span>
                    </div>
                    <div className="flex flex-wrap gap-2">
                       {/* Scan Button */}
                       <button 
                          onClick={handleScanNetwork}
                          disabled={isScanning}
                          className="flex items-center gap-2 bg-indigo-50 border border-indigo-200 text-indigo-700 px-3 py-2 rounded-lg hover:bg-indigo-100 transition text-sm font-medium disabled:opacity-50"
                       >
                          <Radar className={`h-4 w-4 ${isScanning ? 'animate-spin' : ''}`} />
                          {isScanning ? ' ...' : '  '}
                       </button>

                       <button 
                          onClick={handleSync}
                          disabled={isSyncing}
                          className="flex items-center gap-2 bg-white border border-slate-300 text-slate-700 px-3 py-2 rounded-lg hover:bg-slate-50 transition text-sm font-medium disabled:opacity-50"
                       >
                          <RefreshCw className={`h-4 w-4 ${isSyncing ? 'animate-spin text-indigo-600' : ''}`} />
                          {isSyncing ? ' ...' : '  '}
                       </button>
                       <button 
                          onClick={() => setIsAddDeviceMode(true)}
                          className="flex items-center gap-2 bg-indigo-600 text-white px-3 py-2 rounded-lg hover:bg-indigo-700 transition text-sm font-medium"
                       >
                          <Plus className="h-4 w-4" />
                           
                       </button>
                    </div>
                 </div>

                 {/* Discovered Devices Area */}
                 {discoveredDevices.length > 0 && (
                    <div className="mb-6 bg-green-50 border border-green-200 rounded-xl p-4 animate-in slide-in-from-top-2">
                       <h5 className="font-bold text-green-800 text-sm mb-3 flex items-center gap-2">
                          <Wifi className="h-4 w-4" />
                              
                       </h5>
                       <div className="space-y-2">
                          {discoveredDevices.map((dev, idx) => (
                             <div key={idx} className="flex items-center justify-between bg-white p-3 rounded-lg border border-green-100 shadow-sm">
                                <div>
                                   <div className="font-bold text-gray-800 text-sm">{dev.name}</div>
                                   <div className="text-xs text-gray-500 font-mono mt-0.5">{dev.ip}:{dev.port}</div>
                                </div>
                                <div className="flex items-center gap-2">
                                   <span className="text-xs bg-gray-100 px-2 py-1 rounded text-gray-600">{dev.type}</span>
                                   <button 
                                      onClick={() => addDeviceToList(dev)}
                                      className="text-xs bg-green-600 text-white px-3 py-1.5 rounded hover:bg-green-700 transition"
                                   >
                                      
                                   </button>
                                </div>
                             </div>
                          ))}
                       </div>
                    </div>
                 )}

                 {/* Add Device Form */}
                 {isAddDeviceMode && (
                    <form onSubmit={handleAddDevice} className="mb-6 bg-indigo-50 p-4 rounded-xl border border-indigo-100 grid grid-cols-1 md:grid-cols-3 gap-4 items-end">
                       <div className="md:col-span-1">
                          <label className="block text-xs text-indigo-800 mb-1 font-bold">  / </label>
                          <input type="text" required className="w-full p-2 rounded border border-indigo-200 text-sm" placeholder=":  " value={newDevice.name} onChange={e => setNewDevice({...newDevice, name: e.target.value})} />
                       </div>
                       <div>
                          <label className="block text-xs text-indigo-800 mb-1 font-bold">IP Address</label>
                          <input type="text" required className="w-full p-2 rounded border border-indigo-200 text-sm font-mono" placeholder="192.168.1.201" value={newDevice.ip} onChange={e => setNewDevice({...newDevice, ip: e.target.value})} />
                       </div>
                       <div>
                          <label className="block text-xs text-indigo-800 mb-1 font-bold">Port</label>
                          <input type="text" required className="w-full p-2 rounded border border-indigo-200 text-sm font-mono" placeholder="4370" value={newDevice.port} onChange={e => setNewDevice({...newDevice, port: e.target.value})} />
                       </div>
                       <div>
                          <label className="block text-xs text-indigo-800 mb-1 font-bold"> (Model)</label>
                          <select className="w-full p-2 rounded border border-indigo-200 text-sm" value={newDevice.type} onChange={e => setNewDevice({...newDevice, type: e.target.value})}>
                             <option value="ZK">ZK Teco</option>
                             <option value="Timy">Timy</option>
                             <option value="Hikvision">Hikvision</option>
                             <option value="Other">Other (Generic)</option>
                          </select>
                       </div>
                       <div className="flex gap-2">
                          <button type="submit" className="flex-1 bg-indigo-600 text-white p-2 rounded text-sm hover:bg-indigo-700"></button>
                          <button type="button" onClick={() => setIsAddDeviceMode(false)} className="bg-white text-indigo-600 border border-indigo-200 p-2 rounded text-sm hover:bg-indigo-50"></button>
                       </div>
                    </form>
                 )}

                 {/* Devices List */}
                 <div className="space-y-3">
                    {devices.map(device => (
                       <div key={device.id} className="flex items-center justify-between p-4 bg-white border border-slate-200 rounded-lg shadow-sm hover:border-indigo-300 transition-colors group">
                          <div className="flex items-center gap-4">
                             <div className={`p-3 rounded-full ${device.status === 'online' ? 'bg-green-100 text-green-600' : 'bg-red-100 text-red-600'}`}>
                                {device.status === 'online' ? <Wifi className="h-5 w-5" /> : <WifiOff className="h-5 w-5" />}
                             </div>
                             <div>
                                <h5 className="font-bold text-slate-800">{device.name}</h5>
                                <div className="flex items-center gap-3 text-xs text-slate-500 mt-1">
                                   <span className="font-mono bg-slate-100 px-1.5 rounded">{device.ip}:{device.port}</span>
                                   <span className="bg-indigo-50 text-indigo-700 px-1.5 rounded font-medium">{device.type}</span>
                                   <span>{device.location}</span>
                                </div>
                             </div>
                          </div>
                          <div className="flex items-center gap-4">
                             <div className="text-left hidden sm:block">
                                <div className="text-xs text-slate-400 mb-1"> </div>
                                <div className="font-mono text-sm text-slate-700">{device.lastSync}</div>
                             </div>
                             <div className="flex gap-2">
                                <button 
                                  onClick={() => handlePingDevice(device)}
                                  className="p-2 text-slate-400 hover:text-indigo-600 hover:bg-indigo-50 rounded transition-colors" 
                                  title=" "
                                >
                                   <Activity className="h-4 w-4" />
                                </button>
                                <button 
                                  onClick={() => handleDeleteDevice(device.id)}
                                  className="p-2 text-slate-400 hover:text-red-600 hover:bg-red-50 rounded transition-colors" 
                                  title=" "
                                >
                                   <Trash2 className="h-4 w-4" />
                                </button>
                             </div>
                          </div>
                       </div>
                    ))}
                 </div>
              </div>
              
              <div className="p-4 bg-yellow-50 border-t border-yellow-100 text-center rounded-b-2xl">
                 <p className="text-xs text-yellow-800 font-medium flex items-center justify-center gap-2">
                    <AlertTriangle className="h-4 w-4" />
                    :      .          .
                 </p>
              </div>
           </div>
        </div>
      )}
    </div>
  );
};

export default Attendance;
