import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { MOCK_EMPLOYEES } from '../constants';
import { Search, Plus, Filter, MoreVertical, FileText, User, CreditCard, CalendarOff, X } from 'lucide-react';
import { Employee, EmploymentType } from '../types';
import DataControls from '../components/DataControls';

const Employees: React.FC = () => {
  const [employees, setEmployees] = useState<Employee[]>(() => {
    const saved = localStorage.getItem('employees_data');
    return saved ? JSON.parse(saved) : MOCK_EMPLOYEES;
  });
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState<string>('all');
  
  // Modal State
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [newEmp, setNewEmp] = useState({
    name: '', employeeCode: '', nationalId: '', jobTitle: '', department: '', salary: 0
  });

  const navigate = useNavigate();
  const isAdmin = true;

  useEffect(() => { localStorage.setItem('employees_data', JSON.stringify(employees)); }, [employees]);

  const handleAddEmployee = (e: React.FormEvent) => {
    e.preventDefault();
    const emp: Employee = {
      id: `E-${Date.now()}`,
      employeeCode: newEmp.employeeCode,
      name: newEmp.name,
      nationalId: newEmp.nationalId,
      jobTitle: newEmp.jobTitle,
      department: newEmp.department,
      joinDate: new Date().toISOString().split('T')[0],
      salary: Number(newEmp.salary),
      status: 'active',
      avatar: 'https://via.placeholder.com/100',
      contractType: EmploymentType.FULL_TIME
    };
    setEmployees([...employees, emp]);
    setIsAddModalOpen(false);
    setNewEmp({ name: '', employeeCode: '', nationalId: '', jobTitle: '', department: '', salary: 0 });
    alert('   ');
  };

  const filteredEmployees = employees.filter(emp => {
    const matchesSearch = emp.name.includes(searchTerm) || 
                          emp.nationalId.includes(searchTerm) || 
                          emp.department.includes(searchTerm) || 
                          emp.employeeCode.includes(searchTerm);
    const matchesStatus = statusFilter === 'all' || emp.status === statusFilter;
    return matchesSearch && matchesStatus;
  });

  return (
    <div className="space-y-6 animate-in fade-in duration-500">
      <div className="flex flex-col justify-between gap-4 sm:flex-row sm:items-center">
        <div>
          <h2 className="text-2xl font-bold text-gray-800"> </h2>
          <p className="text-sm text-gray-500">     </p>
        </div>
        <div className="flex gap-3">
          <DataControls data={employees} fileName="employees_data" isAdmin={isAdmin} />
          <button onClick={() => setIsAddModalOpen(true)} className="flex items-center gap-2 rounded-lg bg-indigo-600 px-4 py-2 text-white shadow-sm hover:bg-indigo-700 transition">
            <Plus className="h-5 w-5" />
            <span className="hidden sm:inline"> </span>
          </button>
        </div>
      </div>

      <div className="rounded-xl bg-white shadow-sm border border-gray-100">
        <div className="flex flex-col gap-4 border-b border-gray-100 p-4 sm:flex-row sm:items-center">
          <div className="relative flex-1">
            <Search className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 text-gray-400" />
            <input type="text" placeholder="..." className="w-full rounded-lg border border-gray-200 py-2 pr-10 pl-4" value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)} />
          </div>
          <div className="flex items-center gap-2">
            <Filter className="h-5 w-5 text-gray-400" />
            <select value={statusFilter} onChange={(e) => setStatusFilter(e.target.value)} className="rounded-lg border border-gray-200 bg-white py-2 px-4 text-sm">
              <option value="all"> </option>
              <option value="active"></option>
              <option value="on_leave"></option>
              <option value="inactive"></option>
            </select>
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="w-full text-right text-sm">
            <thead className="bg-gray-50 text-gray-500">
              <tr>
                <th className="px-6 py-4"></th>
                <th className="px-6 py-4"></th>
                <th className="px-6 py-4"></th>
                <th className="px-6 py-4"> </th>
                <th className="px-6 py-4"> </th>
                <th className="px-6 py-4"></th>
                <th className="px-6 py-4"></th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-100">
              {filteredEmployees.map((emp) => (
                <tr key={emp.id} onClick={() => navigate(`/employees/${emp.id}`)} className="hover:bg-gray-50 cursor-pointer">
                  <td className="px-6 py-4 font-mono font-bold">{emp.employeeCode}</td>
                  <td className="px-6 py-4 flex items-center gap-3">
                    <img src={emp.avatar} alt="" className="h-8 w-8 rounded-full" />
                    {emp.name}
                  </td>
                  <td className="px-6 py-4">{emp.jobTitle} <br/><span className="text-xs text-gray-500">{emp.department}</span></td>
                  <td className="px-6 py-4 font-mono">{emp.nationalId}</td>
                  <td className="px-6 py-4">{emp.joinDate}</td>
                  <td className="px-6 py-4"><span className={`px-2 py-1 rounded text-xs ${emp.status === 'active' ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}`}>{emp.status}</span></td>
                  <td className="px-6 py-4"><button className="text-indigo-600 hover:underline"></button></td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* Add Modal */}
      {isAddModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4">
          <div className="bg-white w-full max-w-lg rounded-xl p-6 shadow-2xl">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg font-bold">  </h3>
              <button onClick={() => setIsAddModalOpen(false)}><X className="h-5 w-5" /></button>
            </div>
            <form onSubmit={handleAddEmployee} className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <input type="text" required placeholder="" className="border p-2 rounded" value={newEmp.name} onChange={e => setNewEmp({...newEmp, name: e.target.value})} />
                <input type="text" required placeholder=" " className="border p-2 rounded" value={newEmp.employeeCode} onChange={e => setNewEmp({...newEmp, employeeCode: e.target.value})} />
                <input type="text" required placeholder=" " className="border p-2 rounded" value={newEmp.nationalId} onChange={e => setNewEmp({...newEmp, nationalId: e.target.value})} />
                <input type="text" required placeholder="" className="border p-2 rounded" value={newEmp.jobTitle} onChange={e => setNewEmp({...newEmp, jobTitle: e.target.value})} />
                <input type="text" required placeholder="" className="border p-2 rounded" value={newEmp.department} onChange={e => setNewEmp({...newEmp, department: e.target.value})} />
                <input type="number" required placeholder="" className="border p-2 rounded" value={newEmp.salary} onChange={e => setNewEmp({...newEmp, salary: Number(e.target.value)})} />
              </div>
              <button type="submit" className="w-full bg-indigo-600 text-white py-2 rounded font-bold hover:bg-indigo-700"></button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Employees;