import React, { useState } from 'react';
import { MOCK_PAYROLL, MOCK_EMPLOYEES } from '../constants';
import { PayrollRecord } from '../types';
import { DollarSign, Printer, Send, Search, Filter, Calendar, ChevronDown, Eye, X, CheckCircle, FileText } from 'lucide-react'; // Added FileText
import DataControls from '../components/DataControls';

const Payroll: React.FC = () => {
  // Merge employee code into payroll records
  const initialPayroll = MOCK_PAYROLL.map(p => {
     const emp = MOCK_EMPLOYEES.find(e => e.name === p.employeeName);
     return { ...p, employeeCode: emp?.employeeCode || '-' };
  });

  const [payroll, setPayroll] = useState(initialPayroll);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  
  // Slip Modal State
  const [isSlipModalOpen, setIsSlipModalOpen] = useState(false);
  const [selectedSlip, setSelectedSlip] = useState<any>(null);

  const [selectedMonth, setSelectedMonth] = useState<number>(11);
  const [selectedYear, setSelectedYear] = useState<number>(2025);

  const handleApproveAll = () => {
     if (window.confirm('           .')) {
        setPayroll(payroll.map(p => ({...p, status: 'paid'})));
        window.alert('       " "');
     }
  };

  const openSlip = (record: any) => {
     setSelectedSlip(record);
     setIsSlipModalOpen(true);
  };

  const handlePrintSlip = () => {
     window.print(); // Print only the modal content via CSS media queries
  };

  const filteredPayroll = payroll.filter(p => {
    const matchesSearch = p.employeeName.toLowerCase().includes(searchTerm.toLowerCase()) ||
                          p.employeeCode.toLowerCase().includes(searchTerm.toLowerCase());
    return matchesSearch;
  });

  return (
    <div className="space-y-6 animate-in fade-in duration-500">
       {/* Header */}
       <div className="flex justify-between items-center bg-white p-6 rounded-xl shadow-sm no-print">
          <div><h2 className="text-2xl font-bold"> </h2></div>
          <div className="flex gap-2">
             <button onClick={handleApproveAll} className="bg-green-600 text-white px-4 py-2 rounded-lg flex items-center gap-2"><CheckCircle className="w-5 h-5" />  </button>
             <button onClick={() => window.print()} className="bg-indigo-600 text-white px-4 py-2 rounded-lg flex items-center gap-2"><Printer className="w-5 h-5" />  </button>
          </div>
       </div>

       {/* Table */}
       <div className="bg-white rounded-xl shadow-sm overflow-hidden">
          <table className="w-full text-right text-sm">
             <thead className="bg-gray-50">
                <tr>
                   <th className="p-4"></th>
                   <th className="p-4"></th>
                   <th className="p-4"></th>
                   <th className="p-4"></th>
                   <th className="p-4"></th>
                   <th className="p-4"></th>
                   <th className="p-4"></th>
                   <th className="p-4 no-print"></th>
                </tr>
             </thead>
             <tbody>
                {filteredPayroll.map(row => (
                   <tr key={row.id} className="border-b">
                      <td className="p-4 font-mono">{row.employeeCode}</td>
                      <td className="p-4 font-bold">{row.employeeName}</td>
                      <td className="p-4">{row.basicSalary.toLocaleString()}</td>
                      <td className="p-4 text-green-600">+{row.allowances}</td>
                      <td className="p-4 text-blue-600">+{row.transportAllowance}</td>
                      <td className="p-4 font-bold">{row.netSalary.toLocaleString()}</td>
                      <td className="p-4"><span className={`px-2 py-1 rounded text-xs ${row.status === 'paid' ? 'bg-green-100 text-green-800' : 'bg-yellow-100 text-yellow-800'}`}>{row.status === 'paid' ? '' : ''}</span></td>
                      <td className="p-4 no-print">
                         <button onClick={() => openSlip(row)} className="text-indigo-600 hover:bg-indigo-50 p-2 rounded"><FileText className="w-4 h-4" /></button>
                      </td>
                   </tr>
                ))}
             </tbody>
          </table>
       </div>

       {/* Salary Slip Modal */}
       {isSlipModalOpen && selectedSlip && (
          <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4 modal-overlay-print">
             <div className="bg-white w-full max-w-lg rounded-xl shadow-2xl p-8 modal-content-print">
                <div className="flex justify-between items-start border-b pb-4 mb-4">
                   <div>
                      <h2 className="text-xl font-bold text-gray-800"> </h2>
                      <p className="text-sm text-gray-500"> {selectedMonth} / {selectedYear}</p>
                   </div>
                   <div className="text-left">
                      <h3 className="font-bold text-indigo-600">AMA HR</h3>
                      <p className="text-xs text-gray-400">  </p>
                   </div>
                </div>

                <div className="space-y-2 mb-6">
                   <div className="flex justify-between"><span className="text-gray-500"> :</span><span className="font-bold">{selectedSlip.employeeName}</span></div>
                   <div className="flex justify-between"><span className="text-gray-500"> :</span><span className="font-mono">{selectedSlip.employeeCode}</span></div>
                   <div className="flex justify-between"><span className="text-gray-500"> :</span><span>{new Date().toLocaleDateString('ar-EG')}</span></div>
                </div>

                <table className="w-full text-sm mb-6 border">
                   <thead className="bg-gray-50"><tr><th className="p-2 text-right"></th><th className="p-2 text-left"></th></tr></thead>
                   <tbody>
                      <tr className="border-b"><td className="p-2"> </td><td className="p-2 text-left">{selectedSlip.basicSalary.toLocaleString()}</td></tr>
                      <tr className="border-b"><td className="p-2"></td><td className="p-2 text-left text-green-600">+{selectedSlip.allowances.toLocaleString()}</td></tr>
                      <tr className="border-b"><td className="p-2"> </td><td className="p-2 text-left text-green-600">+{selectedSlip.transportAllowance.toLocaleString()}</td></tr>
                      <tr className="border-b"><td className="p-2"></td><td className="p-2 text-left text-green-600">+{selectedSlip.incentives.toLocaleString()}</td></tr>
                      <tr className="border-b"><td className="p-2"></td><td className="p-2 text-left text-red-600">-{selectedSlip.deductions.toLocaleString()}</td></tr>
                      <tr className="bg-gray-100 font-bold"><td className="p-2"> </td><td className="p-2 text-left">{selectedSlip.netSalary.toLocaleString()} .</td></tr>
                   </tbody>
                </table>

                <div className="flex justify-between mt-8 pt-8 border-t">
                   <div className="text-center"><p className="text-xs text-gray-500 mb-4"> </p><div className="w-24 h-px bg-gray-300 mx-auto"></div></div>
                   <div className="text-center"><p className="text-xs text-gray-500 mb-4">  </p><div className="w-24 h-px bg-gray-300 mx-auto"></div></div>
                   <div className="text-center"><p className="text-xs text-gray-500 mb-4"> </p><div className="w-24 h-px bg-gray-300 mx-auto"></div></div>
                </div>

                <div className="mt-8 flex gap-3 no-print">
                   <button onClick={handlePrintSlip} className="flex-1 bg-indigo-600 text-white py-2 rounded-lg hover:bg-indigo-700"></button>
                   <button onClick={() => setIsSlipModalOpen(false)} className="flex-1 bg-gray-100 text-gray-700 py-2 rounded-lg hover:bg-gray-200"></button>
                </div>
             </div>
          </div>
       )}
    </div>
  );
};

export default Payroll;