
// src/services/api.ts

// Get Base URL from settings or default to localhost
export const getBaseUrl = () => {
  const savedUrl = localStorage.getItem('server_url');
  // Remove trailing slash if exists
  return savedUrl ? savedUrl.replace(/\/$/, '') : 'http://localhost:5000/api';
};

// Helper function for requests
const request = async (endpoint: string, options: RequestInit = {}) => {
  const baseUrl = getBaseUrl();
  try {
    const response = await fetch(`${baseUrl}${endpoint}`, {
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
      ...options,
    });

    if (!response.ok) {
      throw new Error(`API Error: ${response.statusText}`);
    }

    return await response.json();
  } catch (error) {
    console.warn('Backend not reachable or Error:', error);
    throw error; // Rethrow to handle in UI
  }
};

export const api = {
  // Check if backend is running
  checkHealth: () => request('/health'),

  // Sync Attendance from a specific device
  syncAttendance: (deviceIp: string, devicePort: string, deviceType: string) => 
    request('/attendance/sync', {
      method: 'POST',
      body: JSON.stringify({ ip: deviceIp, port: devicePort, type: deviceType }),
    }),

  // Get Live GPS Data
  getVehiclesLocation: () => request('/transport/vehicles'),

  // Scan network for devices
  scanForDevices: () => request('/network/scan'),
};
