import React, { useState } from 'react';
import { Users, ArrowLeft, CheckCircle, Lock, User, AlertCircle } from 'lucide-react';
import { MOCK_USERS } from '../constants';
import { SystemUser } from '../types';

interface LoginProps {
  onLogin: (user: SystemUser) => void;
}

const Login: React.FC<LoginProps> = ({ onLogin }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);

    setTimeout(() => {
      const user = MOCK_USERS.find(u => u.username.toLowerCase() === username.toLowerCase());

      if (user) {
        // Simple password check
        if (password.length >= 3) {
           if (user.active) {
             onLogin(user);
           } else {
             setError('      .');
             setIsLoading(false);
           }
        } else {
           setError('   ');
           setIsLoading(false);
        }
      } else {
        setError('   ');
        setIsLoading(false);
      }
    }, 800);
  };

  const fillCredentials = (u: string) => {
    setUsername(u);
    setPassword('123456');
    setError('');
  };

  return (
    <div className="min-h-screen flex flex-col md:flex-row bg-white" dir="rtl">
      <div className="md:w-1/2 bg-indigo-600 text-white p-8 md:p-12 flex flex-col justify-between relative overflow-hidden">
        <div className="absolute top-0 left-0 w-full h-full opacity-10">
           <Users className="w-96 h-96 absolute -bottom-20 -left-20" />
        </div>
        
        <div className="relative z-10">
          <div className="flex items-center gap-2 mb-8">
            <div className="bg-white/20 p-2 rounded-lg backdrop-blur-sm">
              <Users className="h-8 w-8 text-white" />
            </div>
            <h1 className="text-2xl font-bold">AMA HR</h1>
          </div>
          
          <h2 className="text-4xl md:text-5xl font-bold mb-6 leading-tight">
              
          </h2>
          <p className="text-indigo-100 text-lg mb-8 leading-relaxed max-w-md">
             AMA HR      .
          </p>
        </div>

        <div className="relative z-10 space-y-4">
          <div className="flex items-center gap-3">
            <CheckCircle className="h-5 w-5 text-green-400" />
            <span>   </span>
          </div>
          <div className="flex items-center gap-3">
            <CheckCircle className="h-5 w-5 text-green-400" />
            <span>   (HR, Payroll, Transport)</span>
          </div>
        </div>
      </div>

      <div className="md:w-1/2 p-8 md:p-12 flex items-center justify-center bg-gray-50">
        <div className="w-full max-w-md bg-white p-8 rounded-2xl shadow-lg border border-gray-100">
          <div className="text-center mb-8">
            <h3 className="text-2xl font-bold text-gray-900"> </h3>
            <p className="text-gray-500 mt-2">   </p>
          </div>

          {error && (
            <div className="mb-6 p-3 bg-red-50 text-red-700 text-sm rounded-lg flex items-center gap-2 border border-red-100">
              <AlertCircle className="h-4 w-4" />
              {error}
            </div>
          )}

          <form onSubmit={handleLogin} className="space-y-5">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
              <div className="relative">
                <input 
                  type="text" 
                  required
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                  className="w-full px-4 py-3 pl-10 rounded-lg bg-gray-50 border border-gray-200 focus:bg-white focus:border-indigo-500 focus:outline-none transition-all"
                  placeholder="username"
                />
                <User className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-gray-400" />
              </div>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
              <div className="relative">
                <input 
                  type="password" 
                  required
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="w-full px-4 py-3 pl-10 rounded-lg bg-gray-50 border border-gray-200 focus:bg-white focus:border-indigo-500 focus:outline-none transition-all"
                  placeholder=""
                />
                <Lock className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-gray-400" />
              </div>
            </div>

            <button 
              type="submit" 
              disabled={isLoading}
              className="w-full bg-indigo-600 text-white py-3.5 rounded-xl font-bold text-lg shadow-lg hover:bg-indigo-700 transition-all flex items-center justify-center gap-2 group disabled:opacity-70 disabled:cursor-not-allowed"
            >
              {isLoading ? (
                <span className="inline-block h-5 w-5 border-2 border-white border-t-transparent rounded-full animate-spin"></span>
              ) : (
                <>
                  <span></span>
                  <ArrowLeft className="h-5 w-5 group-hover:-translate-x-1 transition-transform" />
                </>
              )}
            </button>
          </form>

          <div className="mt-8 pt-6 border-t border-gray-100">
            <p className="text-xs text-gray-400 text-center mb-3"> :</p>
            <div className="flex flex-wrap gap-2 justify-center">
              <button onClick={() => fillCredentials('admin')} className="text-xs bg-indigo-50 text-indigo-700 px-2 py-1 rounded hover:bg-indigo-100">Admin</button>
              <button onClick={() => fillCredentials('hr_manager')} className="text-xs bg-purple-50 text-purple-700 px-2 py-1 rounded hover:bg-purple-100">HR Manager</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;